import { Component, OnInit} from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment';
import { switchTab, subscribe } from '../app.component';

@Component({
  selector: 'app-films',
  templateUrl: './films.html',
  styleUrls: ['./films.scss']
})
export class FilmsComponent implements OnInit {
  public film: object = {};
  protected url = environment.proxy + 'http://filmapi.andy-riot.com.ua';
  protected slug = '';

  constructor(router: Router,
              private http: HttpClient) {
    this.slug = router.url.slice(6);
    this.film = this.http.get('assets/db.json').subscribe((films: Array<object>) => {
      films.forEach((film) => {
        // @ts-ignore
        if (film.slug === this.slug) {
          this.film = film;
        }
      });
    });
  }

  ngOnInit(): void {
  }

  toggleDescription(ev) {
    ev.preventDefault();
    const el = document.getElementById(ev.target.dataset.target);

    el.classList.toggle('fadeIn');
    el.classList.toggle('fadeOut');

    if (!el.classList.contains('hidden')) {
      scrollTo(0, 0);
      setTimeout(() => el.classList.add('hidden'), 1000);
    } else {
      // scrollTo(0, 0);
      el.classList.remove('hidden');
      scrollTo(0, el.getBoundingClientRect().top - 30);
    }
  }

  switchTab(ev) {
    switchTab(ev);
  }

  subscribe(ev) {
    subscribe(ev);
  }
}
